.app {
  position: relative;
  z-index: 2; }
  .appContainer {
    font-family: Tahoma, Roboto, Helvetica, sans-serif;
    min-width: 320px;
    font-size: 14px;
    line-height: 1.43em;
    padding: 61px 0 0;
    background: #fff; }

.signin {
  box-sizing: border-box;
  position: relative;
  margin: auto;
  padding: 30px 0 90px;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-height: 90vh;
  min-height: calc(100vh - 94px);
  opacity: 0;
  -webkit-transition: opacity .2s ease-out, -webkit-transform .25s ease-in-out;
  transition: opacity .2s ease-out, -webkit-transform .25s ease-in-out;
  transition: transform .25s ease-in-out, opacity .2s ease-out;
  transition: transform .25s ease-in-out, opacity .2s ease-out, -webkit-transform .25s ease-in-out; }
  .signin.is-ready {
    -webkit-transform: none;
            transform: none;
    opacity: 1; }
  .signin-redirectMessage {
    padding-top: 10px; }
  .signin-content {
    position: relative;
    background: #fff;
    box-shadow: 0 5px 25px rgba(215, 215, 215, 0.5);
    border: 1px solid #e5e5e5;
    border-radius: 2px;
    font-size: 14px;
    border-radius: 3px;
    margin: 100px auto auto;
    max-width: 342px;
    width: 100%;
    padding: 10px 12px;
    box-sizing: border-box; }
  .signin-register {
    position: absolute;
    top: 12px;
    right: 12px;
    font-weight: 700;
    font-size: 14px;
    color: #362d90;
    cursor: pointer .signin-register; }
    .signin-register :hover {
      color: #b00; }
    .signin-register:active {
      color: #b00;
      opacity: .7; }
  .signin-main {
    padding-bottom: 5px; }
  .signin-bottom {
    position: relative;
    border-top: 2px solid #e0e0e0;
    margin: 0 -12px;
    padding: 15px 12px 23px; }
  .signin-row, .signin-sent {
    position: relative;
    display: block; }
  .signin-sent {
    background: #f8f8f8;
    border: 1px solid #dedede;
    border-radius: 2px;
    margin: 15px 0 20px;
    padding: 5px 10px;
    display: block;
    box-sizing: border-box; }
  .signin-remind {
    color: #666;
    position: absolute;
    bottom: 7px;
    font-size: 14px;
    right: 10px; }
    .signin-remind.active {
      color: #362d90;
      cursor: pointer; }
      .signin-remind.active:hover {
        color: #b00; }
      .signin-remind.active:active {
        color: #b00;
        opacity: .7; }
  .signin-error {
    color: #b00;
    display: block;
    padding: 3px 0 0; }
  .signin-loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    z-index: 50; }
  .signin-loader .loader {
    position: absolute;
    top: 50%;
    margin-top: -40px;
    padding: 10px;
    background: #fff;
    left: 50%;
    margin-left: -40px;
    border-radius: 5px;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.1); }
  .signin .input {
    margin: 15px 0 20px; }
  .signin-welcome {
    text-align: center; }
  @media (max-width: 1000px) {
    .signin {
      padding-left: 10px;
      padding-right: 10px; } }
  @media (max-width: 580px) {
    .signin-content {
      margin: 30px auto auto; } }

h2 {
  font-size: 24px;
  font-weight: 700;
  line-height: 1.25em; }

.input, .input-label {
  display: block; }

.input-label {
  font-size: 14px;
  color: #000;
  padding-bottom: 5px; }

.input-wrapper {
  position: relative;
  display: block; }

.input-field {
  box-shadow: none;
  -webkit-appearance: none;
  outline: none;
  box-sizing: border-box;
  background: none;
  display: block;
  width: 100%;
  height: 34px;
  font-size: 14px;
  border: 1px solid #e5e5e5;
  border-radius: 2px;
  -webkit-transition: border-color .2s;
  transition: border-color .2s;
  padding: 0 10px 2px;
  background: #fff; }

.input-field:focus {
  border-color: #cacaca; }

.input-field:disabled {
  background: #f8f8f8;
  color: #000;
  -webkit-text-fill-color: #000;
  -webkit-opacity: 1;
  opacity: 1; }

.input-field.has-error, .input-field.is-error, .input-field.is-invalid {
  border-color: rgba(187, 0, 0, 0.6); }

.input-field.is-flickering {
  -webkit-animation: flickText 1s;
  animation: flickText 1s; }

.input textarea {
  resize: none;
  min-height: 34px;
  padding: 7px 10px;
  height: auto;
  box-sizing: border-box;
  font-family: Tahoma,Roboto,Helvetica,sans-serif; }

.input.is-error .input-label, .input.is-invalid .input-label {
  color: #b00; }

.input.is-error .input-field, .input.is-invalid .input-field {
  border-color: rgba(187, 0, 0, 0.25); }

.input-focus {
  display: none; }

.input.is-size-s, .input.is-size-s .input-field {
  display: inline-block;
  width: auto;
  min-width: 0; }

.input.is-size-s .input-field {
  height: 18px;
  font-size: 12px;
  padding: 2px;
  max-width: 100%; }

.btn {
  position: relative;
  display: inline-block;
  font-size: 14px;
  font-family: Tahoma,Roboto,Helvetica,sans-serif;
  cursor: pointer;
  text-decoration: none;
  border-radius: 3px;
  line-height: 1em;
  padding: 4px 10px 5px;
  border: 0;
  outline: none;
  cursor: pointer; }
  .btn:active {
    box-shadow: inset 0 3px 3px rgba(0, 0, 0, 0.05); }
  .btn.is-capitel {
    font-size: 9px;
    letter-spacing: .1em;
    text-transform: uppercase;
    padding-top: 5px; }
  .btn.has-no-hover {
    cursor: default;
    pointer-events: none; }
  .btn--big {
    font-size: 16px; }
  .btn--big, .btn--large {
    padding: 10px 20px 12px;
    line-height: 1em;
    border-radius: 3px; }
  .btn--large {
    font-size: 24px; }
  .btn--blue {
    color: #fff;
    background: #362d90; }
  .btn--blue:active, .btn--blue:hover {
    background: #3a34b0; }
  .btn--blue:active {
    box-shadow: inset 0 3px 3px rgba(0, 0, 0, 0.2); }
  .btn[disabled] {
    background-color: #666;
    opacity: .7;
    cursor: default;
    pointer-events: none;
    -webkit-transition: background-color .15s;
    transition: background-color .15s; }

